
















import { OLabel } from '../label';
import Vue from 'vue';

export default Vue.extend({
  name: 'OTextarea',
  components: { OLabel },
  model: {
    prop: 'vModelValue',
    event: 'input',
  },
  props: {
    id: String,
    vModelValue: [String, Number],
  },
  computed: {
    vModel: {
      get(): unknown {
        return this.vModelValue;
      },
      set(value: unknown): void {
        this.$emit('input', value);
      },
    },
  },
});
